// @flow
import React from 'react';
import { makeStyles } from '@material-ui/core/styles';

type PropsType = {
  src: string,
  alt: string,
  width?: string,
};
const defaultProps = {
  width: '100%',
};

export default function MdImage(props: PropsType) {
  const { src, alt, width = defaultProps.width } = props;
  const classes = makeStyles({
    image: {
      display: 'block',
      marginLeft: 'auto',
      marginRight: 'auto',
      width,
    },
  })();
  return <img src={src} alt={alt} className={classes.image} />;
}

MdImage.defaultProps = defaultProps;
